.single {
  .singleBody {
    max-width: 767px;
    width: 100%;
    .entry-header {
      position: relative;
      margin-bottom: 1rem;
      color: #ddd;
      text-transform: uppercase;
      font-weight: 100;
      font-size: 0.8rem;
      font-family: 'Open Sans';
      .wpulike {
        position: absolute;
        top: 0;
        right: 0;
      }
    }
    .post-thumbnail {
      padding: 15px;
      background: #fff;
      border-radius: 5px;
      border: 1px solid #ddd;
      margin-bottom: 2rem;
    }
  }
  .news-gallery-post {
    max-width: 1100px;
    width: 100%;
  }
  .news-gallery-post .left-news-slider-arrow, .news-gallery-post .right-news-slider-arrow {
    top: 50%;
  }
}